.vouch_to {
  color: $dark;
  margin-left: 20px;
  margin-right: 10px;
  font-size: 1.2rem;
  font-weight: 600;
}
.det_cont_vouch {
  width: 90%;
  height: 5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid rgb(172, 172, 172);
  border-radius: 2px;
  margin-bottom: 0.6rem;
  background-color: white;
  // box-shadow: 2px 2px 5px #a0a0a0;
  transition: 0.3s;
}
.det_cont_vouch:hover {
  box-shadow: 0px 2px 8px #a0a0a0;
}
.det_cont_jovouch {
  width: 90%;
  min-width: 50rem;
  height: 6.4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid rgb(172, 172, 172);
  border-radius: 2px;
  margin-bottom: 0.6rem;
  background-color: white;
  // box-shadow: 2px 2px 5px #a0a0a0;
  padding: 10px;
  transition: 0.3s;
}
.det_cont_jovouch:hover {
  box-shadow: 0px 2px 8px #a0a0a0;
}
.acc_name_vouch {
  color: $pri_color;
  font-size: 1.3rem;
  font-weight: 500;
}
.vouch_costumer_name {
  font-size: 1.3rem;
  color: $dark;
}
.acc_id_vouch {
  margin-right: 5px;
  color: $dark;
  font-size: 1.1rem;
}
.vouch_bill_detail {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 30rem;
  // border: solid black;
}
.det_cont_right_vouch {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 30rem;
}
.det_cont_right_jovouch_m {
  display: flex;
  flex-direction: column;
  // justify-content: space-evenly;
  // align-items: center;
  width: 35rem;
  margin-top: 8px;
  // border: solid black;
  height: 100%;
}

.acc_name_jovouch {
  color: $pri_color;
